<!--
  个人资料页面
  功能需求 : 学生认证;展示房源收藏;展示已租信息;
-->
<template>
    <div>
        <!-- 页头,可以返回主页 -->
        <el-page-header style="margin-left: 2vw;line-height: 70px" @back="$router.push('/')"
                        :content="indexText"></el-page-header>
        <!-- 左上角的用户头像和用户名,点击可以退出登录 -->
        <div style="margin-top: 1vh;width: 8.32vw;height: 10vh;">
            <el-popconfirm
                    confirmButtonText='退出'
                    cancelButtonText='取消'
                    icon="el-icon-info"
                    iconColor="red"
                    @onConfirm="logOut"
                    title="退出登录？">
                <el-avatar
                        slot="reference"
                        src="https://myblog-figure-bed-1258528016.cos.ap-guangzhou.myqcloud.com/%E5%85%B6%E4%BB%96/%E9%BB%91%E4%BA%BA%E6%8A%AC%E6%A3%BA.png"></el-avatar>
            </el-popconfirm>
            <p>{{userData.userName}}</p>
        </div>
        <div>
            <!-- 左边侧栏 -->
            <div class="menuBar el-col-2">
                <el-menu
                        :default-active="activeLeft"
                        @select="changeMenu"
                        class="el-menu-vertical-demo">
                    <el-menu-item index="1">
                        <i class="el-icon-user"></i>
                        <span slot="title">个人资料</span>
                    </el-menu-item>
                    <el-menu-item index="2">
                        <i class="el-icon-menu"></i>
                        <span slot="title">我的收藏</span>
                    </el-menu-item>
                    <el-menu-item index="3">
                        <i class="el-icon-document"></i>
                        <span slot="title">学生认证</span>
                    </el-menu-item>
                    <el-menu-item index="4">
                        <i class="el-icon-setting"></i>
                        <span slot="title">账号设置</span>
                    </el-menu-item>
                </el-menu>
            </div>
            <!--
            根据左边侧栏选择不同,展示不同的页面
            -->
            <!--
            第一个页面: 个人资料
            其中又包含三个子页面: 基本资料、修改头像、密码管理
            -->
            <div v-show="indexText==='个人资料'">
                <el-card class="box-card-person">
                    <div slot="header" class="clearfix">
                        <span>
                            {{userData.userName}}
                            <el-tag v-show="userData.studentAuthentication==='N'" type="danger"
                                    size="mini">未学生认证</el-tag>
                            <el-tag v-show="userData.studentAuthentication==='Y'" type="success">学生认证</el-tag>
                        </span>
                        <el-button style="float: right; padding: 3px 0" type="text" @click="logOut">退出登录</el-button>
                    </div>
                    <el-menu default-active="1" class="el-menu-demo" mode="horizontal" @select="handleSelect">
                        <el-menu-item index="1">基本资料</el-menu-item>
                        <el-menu-item index="2">修改头像</el-menu-item>
                        <el-menu-item index="3">密码管理</el-menu-item>
                    </el-menu>
                </el-card>
                <div class="line"><br></div>

                <el-card v-show="String(currentIndexOfUserInfo)==='1'" class="box-card-person">
                    <div class="text item" style="text-align: left">
                        用户名: {{userData.userName}}<br>
                        手机号: {{userData.userPhone}}<br>
                    </div>
                </el-card>
            </div>
            <!--
            第二个页面: 我的收藏
            -->
            <div v-show="indexText==='我的收藏'">
                <div>
                    <p v-show="houseCollectionLength ===0">您还没有收藏哦,再多逛逛吧</p>
                    <el-row type="flex" justify="center" class="row-bg"
                            v-for="(collection) in parseInt(houseCollectionLength/3)+1"
                            :key="collection"
                            align="middle"
                            style="margin-top: 2%">
                        <!--每行三个数据-->
                        <el-col v-if="index < houseCollectionLength" :span="5" v-for="(item, index) in 3" :key="item"
                                style="margin-left: 2%;margin-right: 2%">
                            <el-card :body-style="{ padding: '0px' }">
                                <img src="https://myblog-figure-bed-1258528016.cos.ap-guangzhou.myqcloud.com/%E5%85%B6%E4%BB%96/house.jpg"
                                     class="image hvr-grow" alt="图片加载失败">
                                <div style="padding-top:14px; padding-bottom: 14px; width: 100%;text-align: center;"
                                     class="hvr-sweep-to-top">
                                    <span>{{houseCollection[index].name}}</span>
                                    <div class="bottom clearfix">
                                        <time class="time">{{houseCollection[index].price}}<br /></time>
                                    </div>
                                </div>
                            </el-card>
                        </el-col>
                    </el-row>
                </div>
            </div>
            <!--
            第三个页面: 学生认证
            -->
            <div v-show="indexText==='学生认证'">
                <el-card class="box-card-person">
                    <el-steps :active="studentAuthenticationSteps" align-center finish-status="success" >
                        <el-step title="填写邮箱"></el-step>
                        <el-step title="查看邮件"></el-step>
                        <el-step title="认证结束"></el-step>
                    </el-steps>
                </el-card>
                <div class="line"><br></div>
                <el-card class="box-card-person">
                    <!--
                    第一步: 填写学校和邮箱
                    -->
                    <div v-show="studentAuthenticationSteps===0" style="width: 20vw;margin-top: 2vh">
                        <el-input
                                style="width: 25vw;margin-left: 15vw"
                                placeholder="请输入学校名称"
                                v-model="studentAuthenticationData.school">
                        </el-input>
                        <el-input
                                style="width: 25vw;margin-left: 15vw;margin-top: 2vh"
                                placeholder="请输入邮箱"
                                v-model="studentAuthenticationData.schoolEmail">
                        </el-input>
                    </div>
                    <!--
                    第二步: 查收邮件
                    -->
                    <div v-show="studentAuthenticationSteps===1">
                        <p>请访问您填写的邮箱收到的邮件中的链接完成学生验证</p>
                    </div>
                    <!--
                    第三步: 检查认证状态(重新获取用户信息)
                    -->
                    <div v-show="studentAuthenticationSteps===2">
                        <p v-show="userData.studentAuthentication==='Y'">您已经完成了学生认证!</p>
                        <p v-show="userData.studentAuthentication==='N'">还没有完成学生认证哦!</p>
                    </div>
                    <el-button style="margin-top: 12px;" @click="next">下一步</el-button>
                </el-card>
            </div>
            <!--
            第四个页面: 账号设置
            -->
            <div v-show="indexText==='账号设置'">
                <p>这里什么都没有哦</p>
            </div>
        </div>
        <Footer />
    </div>
</template>

<script>
import Footer from '../components/Footer'

export default {
  name: 'userData',
  components: {Footer},
  data () {
    return {
      indexText: '个人资料',
      activeLeft: '1',
      currentIndexOfUserInfo: 1,
      // 用户登录标识
      loginCookie: '',
      // 用户信息
      userData: {
        // 用户头像url
        userHead: '',
        // 昵称
        userName: '',
        // 手机号
        userPhone: '',
        // 是否学生认证
        studentAuthentication: ''
      },
      // houseCollectionLength: 2,
      houseCollectionLength: 0,
      // houseCollection: [{
      //   houseId: '198198',
      //   name: '洪湖花园',
      //   rentalTpye: '整租',
      //   floor: '高楼层/19',
      //   area: '37.0㎡',
      //   price: '0元/月',
      //   collectionTime: 1581264000000,
      //   regionCnName: '福田区',
      //   placeCnName: '东门',
      //   pictureUrl: '展示图链接',
      //   houseType: ''
      // },{
      //   houseId: '198198',
      //   name: '洪湖花园',
      //   rentalTpye: '整租',
      //   floor: '高楼层/19',
      //   area: '37.0㎡',
      //   price: '0元/月',
      //   collectionTime: 1581264000000,
      //   regionCnName: '福田区',
      //   placeCnName: '东门',
      //   pictureUrl: '展示图链接',
      //   houseType: ''
      // }]
      houseCollection: [],
      studentAuthenticationSteps: 0,
      studentAuthenticationData: {
        school: '',
        schoolEmail: ''
      }

    }
  },
  watch: {},
  methods: {
    handleSelect (index, indexPath) {
      this.currentIndexOfUserInfo = index
    },
    next () {
      const that = this
      /*
        * 显示加载动画
        */
      const loading = that.$loading({
        lock: true,
        text: 'Loading',
        background: 'rgba(245,245,245,0.7)'
      })
      if (that.studentAuthenticationSteps === 0) {
        /*
          * 调用发送邮件的接口
          */
        that.$http.post('/i/stuAuth/mail', {
          Cookie: that.$cookie.get('token'),
          school: that.studentAuthenticationData.school,
          schoolEmail: that.studentAuthenticationData.schoolEmail
        }, {headers: {'Content-Type': 'application/json'}})
          .then(function (response) {
            loading.close()
            console.log(response)
            // 如果返回的状态码没有错误,就跳转到下一步
            if (response.data.errorCode === 200) {
              if (that.studentAuthenticationSteps++ > 2) {
                that.studentAuthenticationSteps = 0
              }
            }
          })
          .catch(function (error) {
            loading.close()
            that.$notify.error({
              title: '发送验证码失败',
              message: error
            })
          })
      } else if (that.studentAuthenticationSteps === 1) {
        loading.close()
      } else if (that.studentAuthenticationSteps === 2) {
        that.$http.get('/i', {
          params: {
            Cookie: that.$cookie.get('token')
          }
        })
          .then(function (response) {
            if (response.status === 200) {
              console.log(response.data.errorCode + '\t' + response.data.errorMessage)
              if (response.data.errorCode !== 4103) {
                // 将请求到的数据写到用户信息中
                that.userData = response.data.data
                //  如果该用户已经通过学生认证,那么将学生认证界面的步骤条拉满
                if (that.userData.studentAuthentication === 'Y') {
                  that.studentAuthenticationSteps = 2
                }
                console.log(that.userData)
              }
            } else {

            }
          })
          .catch(function (error) {
            console.log(error)
          })
          .then(function () {
            //  停止加载动画
            loading.close()
          })
      }
    },
    studentVerify () {
    },
    /*
    * 左边的菜单栏变化
    */
    changeMenu (index, indexPath) {
      switch (index) {
        default:
        case '1':
          this.indexText = '个人资料'
          break
        case '2':
          this.indexText = '我的收藏'
          const that = this
          that.$http.get('/i/collection', {
            params: {
              Cookie: that.$cookie.get('token')
            }
          })
            .then(function (response) {
              console.log(response.data.errorCode + '\t' + response.data.errorMessage)
              if (response.data.data.length > 0) {
                console.log(response.data.data)
                // 将请求到的数据写到用户信息中
                that.houseCollection = response.data.data
              } else {
                that.houseCollection = []
                that.houseCollectionLength = 0
              }
            })
            .catch(function (error) {
              alert(error)
            })
            .then(function () {
            })
          break
        case '3':
          this.indexText = '学生认证'
          break
        case '4':
          this.indexText = '账号设置'
          break
      }
    },
    /*
    * 退出登录：清除本地 cookie；调用后台接口
   */
    logOut () {
      const that = this
      that.$http.get('/i/logout', {
        params: {
          Cookie: this.$cookie.get('token')
        }
      })
        .then(function (response) {
          if (response.data.errorCode === 200) {
            that.$notify.success({
              title: '注销成功',
              message: '您的账号已退出登录'
            })
            // 删除 token
            that.$cookie.delete('token')
            // 删除 userData
            that.$cookie.delete('userData')
            that.userData = ''
            that.isLogin = false
          } else {
            that.$notify.error({
              title: '注销失败',
              message: response.data.errorMessage
            })
          }
        })
        .catch(function (error) {
          console.log(error)
        })
        .then(function () {
          that.$router.push('/refreshIndex')
        })
    }
  },
  created () {
    const that = this
    if (that.$cookie.get('token')) {
      that.loginCookie = that.$cookie.get('token')
      // 请求获取个人信息
      that.$http.get('/i', {
        params: {
          Cookie: that.$cookie.get('token')
        }
      })
        .then(function (response) {
          if (response.status === 200) {
            console.log(response.data.errorCode + '\t' + response.data.errorMessage)
            if (response.data.errorCode !== 4103) {
              // 将请求到的数据写到用户信息中
              that.userData = response.data.data
              //  如果该用户已经通过学生认证,那么将学生认证界面的步骤条拉满
              if (that.userData.studentAuthentication === 'Y') {
                that.studentAuthenticationSteps = 2
              }
              console.log(that.userData)
            }
          } else {

          }
        })
        .catch(function (error) {
          console.log(error)
        })
        .then(function () {
          // always executed
        })
    } else {
      that.$router.push('/login')
    }
  }
}
</script>

<style scoped>
    .text {
        font-size: 17px;
        line-height: 30px;
        font-family: DengXian_Reg, serif;
    }

    .item {
        margin-bottom: 18px;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }

    .clearfix:after {
        clear: both
    }

    .box-card{
        width: 55vw;
        margin-left: 25vw;
    }

    .box-card-person {
        width: 55vw;
        margin-left: 25vw;
        border: 1px solid #8492a6;
    }
</style>



